body{
  margin: 0;
  color: #3a3a3a;
  /* background-color: #f8f8f8; */
  padding: 3% 5% 0;
}
.loo{
  width: 200px;
  margin: 0;
}
h3{
  font-size: 22px;
  text-align: center;
  color: #000;
}
h3 span{
  font-size: 12px;
  display: block;
}
h4{
  margin-bottom: 0;
  text-align: center;
  font-size: 17px;
  color: #333;
}
header{
  position:fixed;
  height: 100%;
  top:5%;
}
header h1{
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  line-height: 15px;
  list-style-type: none;
  display: block;
  width: 306px;
  height: 80px;
  cursor: pointer;
}
header a{
  margin: 30px 0;
  font-size: 30px;
  text-decoration: none;
  padding-top: 5%;
  color: #555;
  font-weight: bold;
  position: relative;
  display: block;
  /* z-index: 9999;
  cursor: pointer; */
}
header a:nth-of-type(1):hover{
  color: #84b0bd;
}
header a:nth-of-type(2):hover{
  color: #b3acd1;
}
header a:nth-of-type(3):hover{
  color: #be9797;
}
.contents, switch{
  float: right;
  width: 70%;
  text-align: right;
  background: #f6f6f6;
  display: block;
}
.contents1{
  float: right;
  width: 68%;
  /* height: 500px; */
  text-align: right;
  background: #f6f6f6;
  position: relative;
}
.contents1 h3{
  margin-bottom: 0;
}
.contents1 table{
  /* width: 100%; */
  height: 300px;
  background: #f6f6f6;
  display:block;
  overflow-y: scroll;
  z-index: 9999;
}
.contents2{
  float: right;
  width: 75%;
  text-align: right;
  background: #f6f6f6;
  display: block;
}
.boo2{
  margin: 0;
  width: 400px;
  height: 40px;
  font-size: 27px;
  font-weight: 600;
  color: #333;
}
.boo3{
  padding-top:3%;
  padding-bottom: 20%;
  font-size: 14px;
  text-align: left;
}
.boo4{
  padding-top: 15%;
  font-size: 16px;
  color: #000;
}
.boo4 p{
  font-weight: 600;
  font-size: 18px;
  margin-bottom: 5px;
}
.boo4 span{
  font-size: 12px;
}
.boo4 .img120{
  width: 50px;
  padding-right: 5%;
  padding-top: 20px;
}
.boo4 .img114{
  width: 80px;
  padding-right: 10%;
  padding-bottom: 5px;
}
section table{
  margin: 0 auto;
  width: 90%;
  text-align: center;
}
.grap{
  height: 500px;
}
.grap svg{margin: 0;}
.grap svg text{fill:#333 !important}
.tee{
  margin-top: 16px !important;
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  display: block;
}
table{
  border: 1px solid #ddd;
  border-right: 1px solid #ddd;
  border-bottom: 2px solid #6d88b7;
  border-top: 2px solid #6d88b7;
}
thead th{
  padding: 3px 0;
  line-height: 1.25em;
  font-weight: 600;
  word-break: break-all;
  background: #f7faff;
  border-bottom: 1px solid #ddd;
  color: #405985;
  border-right: 1px solid #ddd;
  text-align: center;
}
tbody td{
  /* padding: 3px 0; */
  /* line-height: 1.25em; */
  /* word-break: break-all; */
  text-align: center;
}
caption{
  margin-bottom: 10px;
  font-size: 14px;
}
.grh{
  padding-bottom: 40px;
  padding-top: 5px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
